<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/clausulas">Clausulas</a></li>
            <li class="breadcrumb-item"><a v-bind:href="clause_url">{{clause_name}}</a></li>
            <li class="breadcrumb-item active" aria-current="page">Actualizar Clausula</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <button onclick="window.history.back();" class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </button>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="clauseName">Nombre:</label>
                  <input v-model="clause_name" type="text" class="form-control" id="clauseName" aria-describedby="clauseName" placeholder="Nombre">
                </div>
                <div class="form-group">
                  <label for="clauseDescription">Descripción:</label>
                  <textarea v-model="clause_description" class="form-control" aria-label="clauseDescription"
                  placeholder="Descripción" :rows="6" :max-rows="10"></textarea>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" v-on:click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="save" size="lg"></font-awesome-icon>
                      Guardar
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </button>
                  </div>
                </div>
                <div>&nbsp;</div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      clause_name: "", clause_id: "",
      clause_description: "", clause_url: ""
    }
  },
  mounted () {
    axios
    .get(API_IP+'/clause/'+this.$route.params.id)
    .then(response => {
      this.clause_id = response.data.data._id
      this.clause_name = response.data.data.nombre
      this.clause_description = response.data.data.descripcion
      this.clause_url = "/clausulas/"+response.data.data._id
    })
  },
  methods : {
    submit: function() {
      axios
      .put(API_IP+"/clause/"+this.$route.params.id, {
        _id: this.clause_id,
        nombre: this.clause_name,
        descripcion: this.clause_description
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push({ name: 'clauses' });
    }
  }
}
</script>
