<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/usuarios">Usuarios</a></li>
            <li class="breadcrumb-item"><a v-bind:href="user_url">{{user_name}}</a></li>
            <li class="breadcrumb-item active" aria-current="page">Actualizar Usuario</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" :href="user_url">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </a>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="functionaryName">Nombres del funcionario:</label>
                  <div class="row">
                    <div class="col">
                      <input v-model="name_1" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" :placeholder="name_1">
                    </div>
                    <div class="col">
                      <input v-model="name_2" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" :placeholder="name_2">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="functionaryName">Apellidos del funcionario:</label>
                  <div class="row">
                    <div class="col">
                      <input v-model="lastname_1" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" :placeholder="lastname_1">
                    </div>
                    <div class="col">
                      <input v-model="lastname_2" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" :placeholder="lastname_2">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="documentType">Tipo de documento:</label>
                  <multiselect v-model="document_type" :options="document_types" track-by="_id" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja un tipo de documento">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="documentType">Numero de documento:</label>
                  <input v-model="document_number" type="number" class="form-control" id="documentNumber" aria-describedby="documentNumber" :placeholder="document_number">
                </div>
                <div class="form-group">
                  <label for="location">Dirección:</label>
                  <div class="row">
                    <div class="col">
                      <multiselect v-model="city" :options="cities" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una ciudad">
                      </multiselect>
                    </div>
                    <div class="col">
                      <input type="text" class="form-control" id="userAddress" aria-describedby="userAddress" :placeholder="address">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="contact">Contacto:</label>
                  <div class="row">
                    <div class="col">
                      <input type="number" v-model="phone" class="form-control" id="userPhone" aria-describedby="userPhone" :placeholder="phone">
                    </div>
                    <div class="col">
                      <input type="number" v-model="mobil_phone" class="form-control" id="userMobil" aria-describedby="userMobil" :placeholder="mobil_phone">
                    </div>
                    <div class="col">
                      <input type="number" v-model="extension" class="form-control" id="userExtension" aria-describedby="userExtension" :placeholder="extension">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="username">Usuario:</label>
                  <input type="text" v-model="user_name" class="form-control" id="username" aria-describedby="siaUsername" :placeholder="user_name">
                </div>
                <div class="form-group">
                  <label for="userEmail">Correo</label>
                  <input type="email" v-model="email" class="form-control" id="userEmail" aria-describedby="userEmail" :placeholder="email">
                </div>
                <div class="row">
                  <div class="col">
                    <div class="form-group">
                      <label for="contractStartDate">Fecha inicio contrato:</label>
                      <input type="text" onfocus="(this.type='date')" onblur="(this.type='text')" v-model="start_date" class="form-control" id="contractStartDate" aria-describedby="contractStartDate" :placeholder="start_date">
                    </div>
                  </div>
                  <div class="col">
                    <div class="form-group">
                      <label for="contractEndDate">Fecha culminación contrato:</label>
                      <input type="text" onfocus="(this.type='date')" onblur="(this.type='text')" v-model="end_date" class="form-control" id="contractEndDate" aria-describedby="contractEndDate" :placeholder="end_date">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="userDependency">Dependencia:</label>
                  <multiselect v-model="dependence" :options="dependencies" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una dependencia">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="userRol">Rol:</label>
                  <multiselect v-model="user_roles" :options="roles" :multiple="true" :close-on-select="true" :clear-on-select="false" :hide-selected="true" :preserve-search="true" placeholder="Seleccione los roles del usuario" label="name" track-by="_id" :preselect-first="false">
                  </multiselect>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" v-on:click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="save" size="lg"></font-awesome-icon>
                      Guardar
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" :href="user_url">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-8 text-center">
                    <button class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="unlock" size="lg"></font-awesome-icon>
                      Reestablecer contraseña
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      dependencies: [],
      roles: [],
      cities: [],
      document_types : [] ,
      user_auditoria : {},
      user_id : "",
      user_person_auditoria: {},
      user_person_id: "",
      name_1: "", name_2: "",
      lastname_1: "", lastname_2: "",
      document_type: "", document_number: "",
      city: "", address: "",
      phone: "", mobil_phone: "", extension: "",
      user_name: "", email: "", start_date: "", end_date: "",
      dependence: {}, user_roles: [], user_url: ""
    }
  },
  mounted () {

    axios
    .get(API_IP+'/documentType')
    .then(response => {
      for (var k in response.data.data){
        var doc = {}
        doc["_id"] = response.data.data[k]._id;
        doc["name"] = response.data.data[k].nombre;
        this.document_types.push(doc);
      }
      axios
      .get(API_IP+'/city')
      .then(response => {
        for (var k in response.data.data){
          var city = {}
          city["_id"] = response.data.data[k]._id;
          city["name"] = response.data.data[k].nombre;
          this.cities.push(city);
        }
        axios
        .get(API_IP+'/dependence')
        .then(response => {
          for (var k in response.data.data){
            var dependence = {}
            dependence["_id"] = response.data.data[k]._id;
            dependence["name"] = response.data.data[k].nombre;
            this.dependencies.push(dependence);
          }
          axios
          .get(API_IP+'/rol')
          .then(response => {
            for (var k in response.data.data){
              var rol = {}
              if(response.data.data[k].activo == true ){
                rol["_id"] = response.data.data[k]._id;
                rol["name"] = response.data.data[k].nombre;
                this.roles.push(rol);
              }
            }
          });
        });
      });
    });

    axios
    .get(API_IP+'/user/'+this.$route.params.id)
    .then(response => {
      this.user_auditoria = response.data.data.auditoria
      this.user_id = response.data.data._id
      this.user_person_auditoria = response.data.data.persona.auditoria
      this.user_person_id = response.data.data.persona._id
      this.name_1 = response.data.data.persona.primer_nombre
      this.name_2 = response.data.data.persona.segundo_nombre
      this.lastname_1 = response.data.data.persona.primer_apellido
      this.lastname_2 = response.data.data.persona.segundo_apellido
      this.document_type = {
        "name": response.data.data.persona.tipo_documento.nombre,
        "_id": response.data.data.persona.tipo_documento._id
      }
      this.document_number = response.data.data.persona.numero_documento
      this.city = {
        "name" : response.data.data.persona.ciudad.nombre,
        "_id": response.data.data.persona.ciudad._id
      }
      this.address = response.data.data.persona.direccion
      this.phone = response.data.data.persona.telefono
      this.mobil_phone = response.data.data.persona.celular
      this.extension = response.data.data.persona.extension
      this.user_name = response.data.data.nombre_usuario
      this.email = response.data.data.persona.correo_electronico
      this.start_date = response.data.data.fecha_inicio_contrato.substring(0,10)
      this.end_date = response.data.data.fecha_fin_contrato.substring(0,10)
      this.dependence = {
        "name" : response.data.data.persona.dependencia.nombre,
        "_id" : response.data.data.persona.dependencia._id
      }
      this.user_url = "/usuarios/"+response.data.data._id
      for(var k in response.data.data.roles){
        var rol = {}
        rol["_id"] = response.data.data.roles[k]._id
        rol["name"] = response.data.data.roles[k].nombre
        this.user_roles.push(rol)
      }
    })
  },
  methods: {
    submit: function() {
      var person = {
        "auditoria" : this.user_person_auditoria,
        "_id" : this.user_person_id,
        "dependencia" : this.dependence,
        "numero_documento" : this.document_number,
        "tipo_documento" : this.document_type,
        "primer_nombre" : this.name_1,
        "segundo_nombre" : this.name_2,
        "primer_apellido" : this.lastname_1,
        "segundo_apellido" : this.lastname_2,
        "correo_electronico" : this.email,
        "ciudad" : this.city,
        "direccion" : this.address,
        "telefono" : this.phone,
        "extension" : this.mobil_phone,
        "celular" : this.extension
      }
      axios
      .put(API_IP+"/user/"+this.$route.params.id, {
        auditoria: this.user_auditoria,
        roles: this.user_roles ,
        _id: this.user_id,
        nombre_usuario: this.user_name,
        fecha_fin_contrato: this.end_date,
        fecha_inicio_contrato: this.start_date,
        persona: person
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push({ name: 'showuser', params: { id: this.user_id} });
    }
  }
}
</script>
