<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/presupuestos">Presupuestos</a></li>
            <li class="breadcrumb-item active" aria-current="page">Crear Presupuesto</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/presupuestos">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </a>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="budgetName">Nombre del presupuesto:</label>
                  <input v-model="budget_name" type="text" class="form-control" id="budgetName" aria-describedby="budgetName" placeholder="Nombre">
                </div>
                <div class="form-group">
                  <label for="budgetDependency">Dependencia:</label>
                  <multiselect v-model="budget_dependence" :options="dependencies" :multiple="false" :preserve-search="true" placeholder="Seleccione la dependencia" label="name" track-by="_id" :preselect-first="false">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="budgetValue">Valor:</label>
                  <input v-model="budget_value" type="number" class="form-control" id="budgetValue" aria-describedby="budgetValue" placeholder="Valor">
                </div>
                <div class="form-group">
                  <label for="budgetYear">Año:</label>
                  <input v-model="budget_year" type="number" class="form-control" id="budgetYear" aria-describedby="budgetYear" placeholder="Año">
                </div>
                <div class="form-group">
                  <label for="budgetEntries">Rubros:</label>
                  <multiselect v-model="budget_entries" :options="entries" :multiple="true" :close-on-select="true" :clear-on-select="false" :hide-selected="true" :preserve-search="true" placeholder="Seleccione los rubros" label="name" track-by="rubro" :preselect-first="false">
                  </multiselect>
                </div>
                <div v-for='(entrie, index) in budget_entries' :key='index' class="form-group">
                  <label for="entryValue">Asignar valor a {{entrie.name}}</label>
                  <input v-model="entrie.valor" type="number" class="form-control" id="entryValue" aria-describedby="entryValue" placeholder="Valor del rubro">
                </div>
                {{budget_entries}}
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" @click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                      Crear nuevo
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/presupuestos">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      dependencies: [],
      entries: [],
      budget_name: "",
      budget_dependence: "",
      budget_value: "",
      budget_year: "",
      budget_entries: []
    }
  },
  mounted () {
    axios
    .get(API_IP+"/dependence/")
    .then(response => {
      for (var k in response.data.data){
        var dependency = {}
        dependency["_id"] = response.data.data[k]._id;
        dependency["name"] = response.data.data[k].nombre;
        this.dependencies.push(dependency);
      }

      axios
      .get(API_IP+"/entry/")
      .then(response => {
        for (var k in response.data.data){
          var entry = {}
          entry["rubro"] = response.data.data[k]._id;
          entry["name"] = response.data.data[k].nombre;
          this.entries.push(entry);
        }
      });
    });
  },
  methods: {
    submit: function() {
      this.budget_entries.map(obj =>{
        var rObj = obj;
        rObj["valor"] = parseInt(obj.valor);
        return rObj
      })
      axios
      .post(API_IP+"/budget/", {
        dependencia: this.budget_dependence._id,
        valor: parseInt(this.budget_value),
        año: parseInt(this.budget_year),
        nombre: this.budget_name,
        rubros: this.budget_entries
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push("/presupuestos/");
    }
  }
}
</script>
