<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      personas: [],
      clauses: [],
      causals: [],
      personasSelect: {},
      interventoresSelect:[],
      contratistasSelect:[],
      interventor:"",
      causal: "",
      clausulas: "",
      contratista:""
    }
  },
  props: ['propObj'],
  mounted () {
    axios
    .get(API_IP+"/person/")
    .then(response => {
      for (var k in response.data.data){
        var person = {}
        person["_id"] = response.data.data[k]._id;
        person["name"] = response.data.data[k].primer_nombre+" "+response.data.data[k].primer_apellido;
        this.personas.push(person);
      }
      axios
      .get(API_IP+"/clause/")
      .then(response => {
        for (var k in response.data.data){
          var clause = {}
          clause["_id"] = response.data.data[k]._id;
          clause["name"] = response.data.data[k].nombre
          this.clauses.push(clause);
        }
      })
      axios
      .get(API_IP+"/causalSelection/")
      .then(response => {
        for (var k in response.data.data){
          var causal= {}
          causal["_id"] = response.data.data[k]._id;
          causal["name"] = response.data.data[k].nombre
          this.causals.push(causal);
        }
      })
    })

  },
  methods:{
    optionsSelect(item){
      console.log(this.personasSelect);
      delete this.personasSelect[item.value];
      console.log(this.personasSelect);
    }
  }
}
</script>
