<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/permisos">Permisos</a></li>
            <li class="breadcrumb-item active" aria-current="page">Crear nuevo permiso</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/permisos">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </a>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="permitName">Nombre del permiso:</label>
                  <input type="text" v-model="permit_name" class="form-control" id="permitName" aria-describedby="permitName" placeholder="Nombre">
                </div>
                <div class="form-group">
                  <label for="permitDescription">Descripción:</label>
                  <textarea v-model="permit_description" class="form-control" aria-label="permitDescription"
                  placeholder="Descripción" :rows="6" :max-rows="10"></textarea>
                </div>
                <div class="form-group">
                  <label for="permitCode">Código del permiso:</label>
                  <input type="number" v-model="permit_code" class="form-control" id="permitCode" aria-describedby="permitCode" placeholder="Código">
                </div>
                <div class="form-group">
                  <label for="permitStates">Estado del permiso:</label>
                  <multiselect v-model="permit_state" :options="states" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja un estado">
                  </multiselect>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" @click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                      Crear nuevo
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/permisos">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      permit_description: '',
      permit_name: "",
      states: [
        { name: "Activo", value: "true" },
        { name: "Inactivo", value: "false" }
      ],
      permit_code: "",
      permit_state: " "
    }
  },
  methods: {
    submit: function() {
      axios
      .post(API_IP+"/permit/", {
        activo: this.permit_state.value,
        nombre: this.permit_name,
        descripcion: this.permit_description,
        codigo: this.permit_code
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push("/permisos/");
    }
  }
}
</script>
<style scoped>
#permitStates {
  background-color: white;
}

#rolModules {
  background-color: white;
}
</style>
