<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/dependencias">Dependencias</a></li>
            <li class="breadcrumb-item active" aria-current="page">Crear Dependencia</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/dependencias">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </a>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="dependencyName">Nombre de la dependencia:</label>
                  <input v-model="dependence_name" type="text" class="form-control" id="dependencyName" aria-describedby="dependencyName" placeholder="Nombre">
                </div>
                <div class="form-group">
                  <label for="dependencyDirector">Director de la dependencia:</label>
                  <div class="row">
                    <div class="col">
                      <multiselect v-model="dependence_director" :options="directors" :multiple="false" :preserve-search="true" placeholder="Seleccione el director" label="name" track-by="_id" :preselect-first="false">
                      </multiselect>
                    </div>
                    <div class="col-auto">
                      <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/personas/crear">
                        <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                        Crear Nuevo
                      </a>
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="directorType">Tipo de director:</label>
                  <multiselect v-model="director_type" :options="director_types" :multiple="false" :preserve-search="true" placeholder="Seleccione el tipo del director" :preselect-first="false">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="resolutionNumber">Numero de la resolución:</label>
                  <input v-model="resolution_number" type="text" class="form-control" id="resolutionNumber" aria-describedby="resolutionNumber" placeholder="Resolución">
                </div>
                <div class="form-group">
                  <label for="resolutionDate">Fecha de la resolución:</label>
                  <input v-model="resolution_date" type="date" class="form-control" id="resolutionDate" aria-describedby="resolutionDate">
                </div>
                <div class="form-group">
                  <label for="quipuCode">Código de Quipu:</label>
                  <input v-model="quipu" type="text" class="form-control" id="quipuCode" aria-describedby="quipuCode">
                </div>
                <div class="form-group">
                  <label for="dependenceDescription">Descripción:</label>
                  <textarea v-model="description" class="form-control" aria-label="dependenceDescription"
                  placeholder="Descripción" :rows="6" :max-rows="10"></textarea>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" @click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                      Crear nuevo
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/dependencias">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      directors: [],
      director_types: ["Electo", "Encargado"],
      dependence_name:"",
      resolution_number:"",
      resolution_date:"",
      quipu: "",
      description: "",
      director_type:"",
      dependence_director:{},
      dependency_id:"",
    }
  },
  mounted () {
    axios
    .get(API_IP+"/person/")
    .then(response => {
      for (var k in response.data.data){
        var user_info = {}
        user_info["_id"] = response.data.data[k]._id;
        user_info["name"] = response.data.data[k].primer_nombre+" "+response.data.data[k].segundo_nombre+" "+response.data.data[k].primer_apellido+" "+response.data.data[k].segundo_apellido;
        this.directors.push(user_info);
      }
    });
  },
  methods: {
    submit: function() {
      axios
      .post(API_IP+"/dependence/", {
        nombre: this.dependence_name,
        resolucion_numero: this.resolution_number,
        resolucion_fecha: this.resolution_date,
        quipu_codigo: this.quipu,
        descripcion: this.description,
        director_tipo: this.director_type,
        director: this.dependence_director._id
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push("/dependencias/");
    }
  }
}
</script>

<style scoped>
#dependencyDirector {
  background-color: white;
}

#directorType {
  background-color: white;
}
</style>
