<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item active" aria-current="page">Solicitudes</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <button onclick="window.history.back();" class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </button>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row">
            <div class="col-auto mr-auto">
              <h2 style="color:#003e1e">
                ODS
              </h2>
            </div>
            <div class="col-auto">
              <router-link class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" :to="{ name: 'createrequest', params: { type: 'ods', tipo_solicitud: 'ODS' } }">
                <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                Crear
              </router-link>
            </div>
          </div>
          <br>
          <div class="row">
            <div class="col">
              <table class="table table-striped" style="text-align:center;background-color:#dddddd;">
                <thead style="background-color: #003e1e; color:white;">
                  <tr>
                    <th scope="col">Número</th>
                    <th scope="col">Tipo de solicitud</th>
                    <th scope="col">Valor</th>
                    <th scope="col">Estado</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="clickable-row" v-for='(ods, index) in odss' :key='index'>
                    <td><a v-bind:href="ods.url">{{ods.number}}</a></td>
                    <td>{{ods.type}}</td>
                    <td>{{ods.value}}</td>
                    <td>{{ods.state}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div>&nbsp;</div>
          <div>&nbsp;</div>
          <div class="row">
            <div class="col-auto mr-auto">
              <h2 style="color:#003e1e">
                ODC
              </h2>
            </div>
            <div class="col-auto">
              <router-link class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" :to="{ name: 'createrequest', params: { type: 'odc', tipo_solicitud: 'ODC' } }">
                <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                Crear
              </router-link>
            </div>
          </div>
          <br>
          <div class="row">
            <div class="col">
              <table class="table table-striped" style="text-align:center;background-color:#dddddd;">
                <thead style="background-color: #003e1e; color:white;">
                  <tr>
                    <th scope="col">Número</th>
                    <th scope="col">Tipo de solicitud</th>
                    <th scope="col">Valor</th>
                    <th scope="col">Estado</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="clickable-row" v-for='(odc, index) in odcs' :key='index'>
                    <td><a v-bind:href="odc.url">{{odc.number}}</a></td>
                    <td>{{odc.type}}</td>
                    <td>{{odc.value}}</td>
                    <td>{{odc.state}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div>&nbsp;</div>
          <div>&nbsp;</div>
          <div class="row">
            <div class="col-auto mr-auto">
              <h2 style="color:#003e1e">
                ATI
              </h2>
            </div>
            <div class="col-auto">
              <router-link class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" :to="{ name: 'createrequest', params: { type: 'ati', tipo_solicitud: 'ATI' } }">
                <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                Crear
              </router-link>
            </div>
          </div>
          <br>
          <div class="row">
            <div class="col">
              <table class="table table-striped" style="text-align:center;background-color:#dddddd;">
                <thead style="background-color: #003e1e; color:white;">
                  <tr>
                    <th scope="col">Número</th>
                    <th scope="col">Tipo de solicitud</th>
                    <th scope="col">Valor</th>
                    <th scope="col">Estado</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="clickable-row" v-for='(ati, index) in atis' :key='index'>
                    <td><a v-bind:href="ati.url">{{ati.number}}</a></td>
                    <td>{{ati.type}}</td>
                    <td>{{ati.value}}</td>
                    <td>{{ati.state}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  data () {
    return {
      odss: [],
      odcs: [],
      atis: []
    }
  },
  mounted () {
    axios
    .get(API_IP+'/request')
    .then(response => {
      for(var k in response.data.data){
        var request = {}
        request["id"] = response.data.data[k]._id
        request["number"] = response.data.data[k].numero_solicitud
        request["type"] = response.data.data[k].tipo_solicitud.nombre
        request["value"] = response.data.data[k].valor_solicitud
        request["state"] = response.data.data[k].estado
        request["url"] = "/solicitudes/"+response.data.data[k].tipo_solicitud.nombre.toLowerCase()+"/"+response.data.data[k]._id;

        if (request.type == "ODS") {
          this.odss.push(request);
        } else if (request.type == "ODC") {
          this.odcs.push(request);
        } else if (request.type == "ATI") {
          this.atis.push(request);
        }
      }
    })
  }
}
</script>
