<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/modulos">Módulos</a></li>
            <li class="breadcrumb-item active" aria-current="page">Crear Módulo</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/modulos">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </a>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="moduleName">Nombre del módulo:</label>
                  <input type="text" v-model="module_name" class="form-control" id="moduleName" aria-describedby="moduleName" placeholder="Nombre">
                </div>
                <div class="form-group">
                  <label for="moduleDescription">Descripción:</label>
                  <textarea v-model="module_description" class="form-control" aria-label="moduleDescription"
                    placeholder="Descripción" :rows="6" :max-rows="10"></textarea>
                </div>
                <div class="form-group">
                  <label for="modulePermits">Permisos del módulo:</label>
                  <multiselect v-model="module_permits" :options="permits" :multiple="true" :close-on-select="true" :clear-on-select="false" :hide-selected="true" :preserve-search="true" placeholder="Seleccione los permisos del módulo" label="name" track-by="_id" :preselect-first="false">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="moduleCode">Código del módulo:</label>
                  <input type="number" v-model="module_code" class="form-control" id="moduleCode" aria-describedby="moduleCode" placeholder="Código">
                </div>
                <div class="form-group">
                  <label for="moduleState">Estado del módulo:</label>
                  <multiselect v-model="module_state" :options="states" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja un estado">
                  </multiselect>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" @click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                      Crear nuevo
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/modulos">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      module_permits: [],
      mod_permits: [],
      module_state: "",
      module_name: "",
      permits: [],
      states: [
        { name: "Activo", activo: "true" },
        { name: "Inactivo", activo: "false" }
      ],
      module_description: "",
      module_code: ""
    }
  },
  mounted () {
    axios
    .get(API_IP+"/permit/")
    .then(response => {
      for(var k in response.data.data){
        var per = {}
        if(response.data.data[k].activo == true ){
          per["name"] = response.data.data[k].nombre;
          per["_id"] = response.data.data[k]._id;
          this.permits.push(per);
        }
      }
    });
  },
  methods: {
    submit: function() {
      axios
      .post(API_IP+"/module/", {
        activo: this.module_state.activo,
        nombre: this.module_name,
        descripcion: this.module_description,
        codigo: this.module_code,
        permisos: this.module_permits
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push("/modulos/");
    }
  }
}
</script>
