<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/personas">Personas</a></li>
            <li class="breadcrumb-item"><a v-bind:href="person_url">{{person_name}}</a></li>
            <li class="breadcrumb-item active" aria-current="page">Actualizar Usuario</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <button onclick="window.history.back();" class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </button>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="personName">Nombres:</label>
                  <div class="row">
                    <div class="col">
                      <input v-model="name_1" type="text" class="form-control" id="personName" aria-describedby="personName" placeholder="Primer Nombre">
                    </div>
                    <div class="col">
                      <input v-model="name_2" type="text" class="form-control" id="personName" aria-describedby="personName" placeholder="Segundo Nombre">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="personName">Apellidos:</label>
                  <div class="row">
                    <div class="col">
                      <input v-model="lastname_1" type="text" class="form-control" id="personName" aria-describedby="personName" placeholder="Primer Apellido">
                    </div>
                    <div class="col">
                      <input v-model="lastname_2" type="text" class="form-control" id="personName" aria-describedby="personName" placeholder="Segundo Apellido">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="documentType">Tipo de documento:</label>
                  <multiselect v-model="document_type" :options="document_types" track-by="_id" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja un tipo de documento">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="documentType">Numero de documento:</label>
                  <input v-model="document_number" type="number" class="form-control" id="documentNumber" aria-describedby="documentNumber" placeholder="Numero">
                </div>
                <div class="form-group">
                  <label for="location">Dirección:</label>
                  <div class="row">
                    <div class="col">
                      <multiselect v-model="city" :options="cities" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una ciudad">
                      </multiselect>
                    </div>
                    <div class="col">
                      <input type="text" v-model="address" class="form-control" id="userAddress" aria-describedby="userAddress" placeholder="Dirección">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="contact">Contacto:</label>
                  <div class="row">
                    <div class="col">
                      <input type="number" v-model="phone" class="form-control" id="userPhone" aria-describedby="userPhone" placeholder="Teléfono">
                    </div>
                    <div class="col">
                      <input type="number" v-model="mobil_phone" class="form-control" id="userMobil" aria-describedby="userMobil" placeholder="Celular">
                    </div>
                    <div class="col">
                      <input type="number" v-model="extension" class="form-control" id="userExtension" aria-describedby="userExtension" placeholder="Extensión">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="userEmail">Correo</label>
                  <input type="email" v-model="email" class="form-control" id="userEmail" aria-describedby="userEmail">
                </div>
                <div class="form-group">
                  <label for="userDependency">Dependencia:</label>
                  <multiselect v-model="dependence" :options="dependencies" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una dependencia">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="personType">Tipo de persona:</label>
                  <multiselect v-model="person_type" :options="person_types" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja un tipo de persona">
                  </multiselect>
                </div>
                <div v-if=" person_type === 'Profesor' ">
                  {{student_program = "" }}
                  <div class="form-group">
                    <label for="teacherType">Tipo de profesor:</label>
                    <input type="text" v-model="teacher_type" class="form-control" id="teacherType" aria-describedby="teacherType">
                  </div>
                  <div class="form-group">
                    <label for="teacherOcupation">Dedicación:</label>
                    <input type="text" v-model="teacher_ocupation" class="form-control" id="teacherOcupation" aria-describedby="teacherOcupation">
                  </div>
                </div>
                <div v-else-if=" person_type === 'Estudiante' ">
                  {{teacher_type = ""}}
                  {{teacher_ocupation = ""}}
                  <div class="form-group">
                    <label for="studentProgram">Programa:</label>
                    <multiselect v-model="student_program" :options="programs" track-by="_id" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una programa">
                    </multiselect>
                  </div>
                </div>
                <div v-else-if=" person_type === 'Trabajador' " class="form-group">
                  <!-- TODO -->
                  TODO
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" v-on:click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="save" size="lg"></font-awesome-icon>
                      Guardar
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" v-bind:href="person_url">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
                <div>&nbsp;</div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      dependencies: [],
      cities: [],
      document_types : [],
      programs : [],
      person_types: ['Estudiante', 'Profesor', 'Trabajador'],
      person_auditoria : {},
      person_id : "",
      name_1: "", name_2: "",
      lastname_1: "", lastname_2: "",
      document_type: "", document_number: "",
      city: "", address: "",
      phone: "", mobil_phone: "", extension: "",
      person_name: "", email: "", start_date: "", end_date: "",
      dependence: {}, person_url: "",
      person_type: "",
      student_program: "",
      teacher_type: "", teacher_ocupation: ""
    }
  },
  mounted () {
    axios
    .get(API_IP+'/documentType')
    .then(response => {
      for (var k in response.data.data){
        var doc = {}
        doc["_id"] = response.data.data[k]._id;
        doc["name"] = response.data.data[k].nombre;
        this.document_types.push(doc);
      }
      axios
      .get(API_IP+'/city')
      .then(response => {
        for (var k in response.data.data){
          var city = {}
          city["_id"] = response.data.data[k]._id;
          city["name"] = response.data.data[k].nombre;
          this.cities.push(city);
        }
        axios
        .get(API_IP+'/dependence')
        .then(response => {
          for (var k in response.data.data){
            var dependence = {}
            dependence["_id"] = response.data.data[k]._id;
            dependence["name"] = response.data.data[k].nombre;
            this.dependencies.push(dependence);
          }
          axios
          .get(API_IP+'/program')
          .then(response => {
            for (var k in response.data.data){
              var program = {}
              program["_id"] = response.data.data[k]._id;
              program["name"] = response.data.data[k].nombre;
              this.programs.push(program);
            }
          });
        });
      });
    });

    axios
    .get(API_IP+'/person/'+this.$route.params.id)
    .then(response => {
      this.person_auditoria = response.data.data.auditoria
      this.person_id = response.data.data._id
      this.name_1 = response.data.data.primer_nombre
      this.name_2 = response.data.data.segundo_nombre
      this.lastname_1 = response.data.data.primer_apellido
      this.lastname_2 = response.data.data.segundo_apellido
      this.document_type = {
        "name": response.data.data.tipo_documento.nombre,
        "_id": response.data.data.tipo_documento._id
      }
      this.document_number = response.data.data.numero_documento
      this.city = {
        "name" : response.data.data.ciudad.nombre,
        "_id": response.data.data.ciudad._id
      }
      this.address = response.data.data.direccion
      this.phone = response.data.data.telefono
      this.mobil_phone = response.data.data.celular
      this.extension = response.data.data.extension
      this.person_name = response.data.data.primer_nombre+" "+response.data.data.primer_apellido
      this.email = response.data.data.correo_electronico
      this.dependence = {
        "name" : response.data.data.dependencia.nombre,
        "_id" : response.data.data.dependencia._id
      }
      if (response.data.data.estudiante !== undefined && response.data.data.estudiante !== null) {
        this.student_program = {
            "name" : response.data.data.estudiante.programa.nombre,
            "_id" : response.data.data.estudiante.programa._id
        }
        this.person_type = "Estudiante"
      } else {
        this.student_program = ""
        this.person_type = "Profesor"
      }
      this.teacher_type = response.data.data.profesor.tipo
      this.teacher_ocupation = response.data.data.profesor.dedicacion
      this.person_url = "/personas/"+response.data.data._id
    })
  },
  methods: {
    submit: function() {
      axios
      .put(API_IP+"/person/"+this.$route.params.id, {
        auditoria : this.person_auditoria,
        _id : this.person_id,
        dependencia : this.dependence,
        numero_documento : this.document_number,
        tipo_documento : this.document_type,
        primer_nombre : this.name_1,
        segundo_nombre : this.name_2,
        primer_apellido : this.lastname_1,
        segundo_apellido : this.lastname_2,
        correo_electronico : this.email,
        ciudad : this.city,
        direccion : this.address,
        telefono : this.phone,
        extension : this.mobil_phone,
        celular : this.extension,
        profesor: {
          tipo: this.teacher_type,
          dedicacion: this.teacher_ocupation
        },
        estudiante: {
          programa: this.student_program._id
        }
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push({ name: 'showperson', params: { id: this.person_id} });
    }
  }
}
</script>
