<template>
  <div class="container-fluid">
    <div style="margin:40px;background-color:rgba(255, 255, 255, 0.7);">
      <div class="row">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item"><a href="/">Home</a></li>
            <li class="breadcrumb-item"><a href="/usuarios">Usuarios</a></li>
            <li class="breadcrumb-item active" aria-current="page">Crear Usuario</li>
          </ol>
        </nav>
      </div>
      <div class="row">
        <div class="col-md-8 offset-md-2" style="margin-bottom:80px;">
          <div class="row">
            <div class="col">
              <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/usuarios">
                <font-awesome-icon icon="arrow-left" size="lg"></font-awesome-icon>
              </a>
            </div>
          </div>
          <div>&nbsp;</div>
          <div class="row justify-content-center">
            <div class="col-5 align-self-center">
              <form>
                <div class="form-group">
                  <label for="functionaryName">Nombres del funcionario:</label>
                  <div class="row">
                    <div class="col">
                      <input v-model="name_1" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" placeholder="Primer Nombre">
                    </div>
                    <div class="col">
                      <input v-model="name_2" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" placeholder="Segundo Nombre">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="functionaryName">Apellidos del funcionario:</label>
                  <div class="row">
                    <div class="col">
                      <input v-model="lastname_1" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" placeholder="Primer Apellido">
                    </div>
                    <div class="col">
                      <input v-model="lastname_2" type="text" class="form-control" id="functionaryName" aria-describedby="functionaryName" placeholder="Segundo Apellido">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="documentType">Tipo de documento:</label>
                  <multiselect v-model="document_type" :options="document_types" track-by="_id" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja un tipo de documento">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="documentType">Numero de documento:</label>
                  <input v-model="document_number" type="number" class="form-control" id="documentNumber" aria-describedby="documentNumber" placeholder="Numero">
                </div>
                <div class="form-group">
                  <label for="location">Dirección:</label>
                  <div class="row">
                    <div class="col">
                      <multiselect v-model="city" :options="cities" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una ciudad">
                      </multiselect>
                    </div>
                    <div class="col">
                      <input type="text" v-model="address" class="form-control" id="userAddress" aria-describedby="userAddress" placeholder="Dirección">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="contact">Contacto:</label>
                  <div class="row">
                    <div class="col">
                      <input type="number" v-model="phone" class="form-control" id="userPhone" aria-describedby="userPhone" placeholder="Teléfono">
                    </div>
                    <div class="col">
                      <input type="number" v-model="mobil_phone" class="form-control" id="userMobil" aria-describedby="userMobil" placeholder="Celular">
                    </div>
                    <div class="col">
                      <input type="number" v-model="extension" class="form-control" id="userExtension" aria-describedby="userExtension" placeholder="Extensión">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="username">Usuario:</label>
                  <input type="text" v-model="user_name" class="form-control" id="username" aria-describedby="siaUsername">
                </div>
                <div class="form-group">
                  <label for="userEmail">Correo</label>
                  <input type="email" v-model="email" class="form-control" id="userEmail" aria-describedby="userEmail">
                </div>
                <div class="row">
                  <div class="col">
                    <div class="form-group">
                      <label for="contractStartDate">Fecha inicio contrato:</label>
                      <input type="date" v-model="start_date" class="form-control" id="contractStartDate" aria-describedby="contractStartDate">
                    </div>
                  </div>
                  <div class="col">
                    <div class="form-group">
                      <label for="contractEndDate">Fecha culminación contrato:</label>
                      <input type="date" v-model="end_date" class="form-control" id="contractEndDate" aria-describedby="contractEndDate">
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="userDependency">Dependencia:</label>
                  <multiselect v-model="dependence" :options="dependencies" track-by="name" label="name" :searchable="false" :close-on-select="true" :show-labels="true" placeholder="Escoja una dependencia">
                  </multiselect>
                </div>
                <div class="form-group">
                  <label for="userRol">Rol:</label>
                  <multiselect v-model="user_roles" :options="roles" :multiple="true" :close-on-select="true" :clear-on-select="false" :hide-selected="true" :preserve-search="true" placeholder="Seleccione los roles del usuario" label="name" track-by="_id" :preselect-first="false">
                  </multiselect>
                </div>
                <div>&nbsp;</div>
                <div class="row justify-content-center">
                  <div class="col-4 text-center">
                    <button class="btn btn-primary" @click="submit()" style="background:#003e1e;border-color:#003e1e;">
                      <font-awesome-icon icon="pencil-alt" size="lg"></font-awesome-icon>
                      Crear nuevo
                    </button>
                  </div>
                  <div class="col-4 text-center">
                    <a class="btn btn-primary" style="background:#003e1e;border-color:#003e1e;" href="/usuarios">
                      <font-awesome-icon icon="times-circle" size="lg"></font-awesome-icon>
                      Cancelar
                    </a>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import {axios} from '../../helpers/axios';
var API_IP = process.env.VUE_APP_API_IP

export default {
  components: {
    Multiselect
  },
  data () {
    return {
      dependencies: [],
      roles: [],
      cities: [],
      document_types : [] ,
      name_1: "", name_2: "",
      lastname_1: "", lastname_2: "",
      document_type: "",
      document_number: "",
      city: "",
      address: "",
      phone: "", mobil_phone: "", extension: "",
      user_name: "",
      email: "",
      start_date: "",
      end_date: "",
      dependence: "",
      user_roles: []
    }
  },
  mounted () {
    axios
    .get(API_IP+'/documentType')
    .then(response => {
      for (var k in response.data.data){
        var doc = {}
        doc["_id"] = response.data.data[k]._id;
        doc["name"] = response.data.data[k].nombre;
        this.document_types.push(doc);
      }
      axios
      .get(API_IP+'/city')
      .then(response => {
        for (var k in response.data.data){
          var city = {}
          city["_id"] = response.data.data[k]._id;
          city["name"] = response.data.data[k].nombre;
          this.cities.push(city);
        }
        axios
        .get(API_IP+'/dependence')
        .then(response => {
          for (var k in response.data.data){
            var dependence = {}
            dependence["_id"] = response.data.data[k]._id;
            dependence["name"] = response.data.data[k].nombre;
            this.dependencies.push(dependence);
          }
          axios
          .get(API_IP+'/rol')
          .then(response => {
            for (var k in response.data.data){
              var rol = {}
              if(response.data.data[k].activo == true ){
                rol["_id"] = response.data.data[k]._id;
                rol["name"] = response.data.data[k].nombre;
                this.roles.push(rol);
              }
            }
          });
        });
      });
    });
  },
  methods : {
    submit: function() {
      var roles_usuario = []
      for (var k in this.user_roles){
        var rol = {}
        rol["_id"] = this.user_roles[k]._id;
        roles_usuario.push(rol);
      }

      var person = {}
      person["dependencia"] = this.dependence._id;
      person["numero_documento"] = this.document_number;
      person["tipo_documento"] = this.document_type._id;
      person["primer_nombre"] = this.name_1;
      person["segundo_nombre"] = this.name_2;
      person["primer_apellido"] = this.lastname_1;
      person["segundo_apellido"] = this.lastname_2;
      person["correo_electronico"] = this.email;
      person["ciudad"] = this.city._id;
      person["direccion"] = this.address;
      person["telefono"] = this.phone;
      person["extension"] = this.mobil_phone;
      person["celular"] = this.extension;

      axios
      .post(API_IP+"/user/", {
        roles: roles_usuario,
        nombre_usuario: this.user_name,
        fecha_fin_contrato: this.end_date,
        fecha_inicio_contrato: this.start_date,
        persona: person
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
      this.$router.push({ name: 'users' });
    }
  }
}
</script>

<style scoped>
#userDependency {
  background-color: white;
}

#userRol {
  background-color: white;
}

#documentType {
  background-color: white;
}

#userCity {
  background-color: white;
}

